import { paste } from "@testing-library/user-event/dist/paste";
import React, { useEffect, useRef, useState } from "react";

const Conjugation = ({ verbs }) => {
  const grammar = [
    {
      id: 1,
      title: "present",
      polite: "",
      regular: "",
      negativePolite: "",
      negativeRegular: "",
    },
    {
      id: 2,
      title: "past",
      polite: "",
      regular: "",
      negativePolite: "",
      negativeRegular: "",
    },
    {
      id: 3,
      title: "volitional",
      polite: "",
      regular: "",
      negativePolite: "",
      negativeRegular: "",
    },
    {
      id: 4,
      title: "imperative",
      polite: "",
      regular: "",
      negativePolite: "",
      negativeRegular: "",
    },
    {
      id: 5,
      title: "conditional",
      polite: "",
      regular: "",
      negativePolite: "",
      negativeRegular: "",
    },
    {
      id: 6,
      title: "potential",
      polite: "",
      regular: "",
      negativePolite: "",
      negativeRegular: "",
    },
    {
      id: 7,
      title: "passive",
      polite: "",
      regular: "",
      negativePolite: "",
      negativeRegular: "",
    },
  ];
  let polite = useRef();
  let regular = useRef();
  let negativePolite = useRef();
  let negativeRegular = useRef();
  const [stem, setStem] = useState("");
  const [negStem, setNegStem] = useState("");
  const [first, setFirst] = useState("");
  const [second, setSecond] = useState("");
  const [third, setThird] = useState("");
  const [fourth, setFourth] = useState("");
  const [fifth, setFifth] = useState("");
  const [tForm, setTForm] = useState("");
  let verb = verbs.Japanese;

  console.log(window.location.pathname.substring(1));
  useEffect(() => {
    const getStem = () => {
      setStem(verb.slice(0, -1));
      if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      } else if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      } else if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      } else if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      } else if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      } else if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      } else if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      } else if (verb.charAt(verb.length - 1) === "") {
        setFirst(stem + "");
        setSecond(stem + "");
        // setThird(stem + "");
        setFourth(stem + "");
        setFifth(stem + "");
        setTForm(stem + "");
      }
    };
    getStem();
  }, [verb, first, second, third, fourth, fifth, stem]);
  console.log(stem, first, second, fourth, fifth);
  console.log(verbs.class);
  return (
    <div className=" conjugation-container">
      <h1>Conjugation table for verb {verb}</h1>
      <div className="tables">
        <div className="info-table">
          <div className="info">
            <p>Verb:</p> {verb}
          </div>
          <div className="info">
            <p>Hiragana:</p> {verbs.Hiragana}
          </div>
          <div className="info">
            <p>Stem:</p> {stem}
          </div>
          <div className="info">
            <p>Romaji:</p> {verbs.Romanization}
          </div>
          <div className="info">
            <p>Class:</p>
            {verbs.class}
          </div>
        </div>
        <div className="conjugation-table">
          <table>
            <thead>
              <td>tense</td>
              <td>type</td>
              <td>Positive (+)</td>
              <td>Negative (-)</td>
            </thead>

            {grammar.map((g, index) => (
              <tbody key={index}>
                <tr>
                  <td className="table-row-title" rowSpan="2">
                    {g.title}
                  </td>
                  <td>plain</td>
                  {verbs.class === 2 ? (
                    <td ref={regular}>
                      {g.title === "volitional"
                        ? stem + ""
                        : g.title === "imperative"
                        ? fifth
                        : g.title === "potential"
                        ? first + ""
                        : g.title === "passive"
                        ? stem
                        : stem}
                      {g.regular}
                    </td>
                  ) : (
                    <td ref={regular}>
                      {g.title === "past" || g.title === "conditional"
                        ? tForm
                        : g.title === "present"
                        ? stem
                        : g.title === "imperative" || g.title === "potential"
                        ? fourth
                        : g.title === "volitional"
                        ? fifth
                        : first}
                      {g.regular}
                    </td>
                  )}
                  {verbs.class === 2 ? (
                    <td ref={negativeRegular}>
                      {g.title === "imperative"
                        ? verb
                        : g.title === "potential"
                        ? first + ""
                        : stem}
                      {g.negativeRegular}
                    </td>
                  ) : (
                    <td ref={negativeRegular}>
                      {g.title === "potential"
                        ? fourth
                        : g.title === "imperative"
                        ? verb
                        : first}
                      {g.negativeRegular}
                    </td>
                  )}
                </tr>
                <tr>
                  <td>Polite</td>
                  {verbs.class === 2 ? (
                    <td ref={polite}>
                      {g.title === "potential" ? first : stem}
                      {g.polite}
                    </td>
                  ) : (
                    <td ref={polite}>
                      {g.title === "imperative"
                        ? tForm
                        : g.title === "passive"
                        ? first
                        : second}
                      {g.polite}
                    </td>
                  )}
                  {verbs.class === 2 ? (
                    <td ref={negativePolite}>
                      {g.title === "potential" ? first : stem}
                      {g.negativePolite}
                    </td>
                  ) : (
                    <td ref={negativePolite}>
                      {g.title === "present" ||
                      g.title === "past" ||
                      g.title === "conditional"
                        ? second
                        : g.title === "potential"
                        ? fourth
                        : first}
                      {g.negativePolite}
                    </td>
                  )}
                </tr>
              </tbody>
            ))}
          </table>
        </div>
      </div>
    </div>
  );
};

export default Conjugation;
